.form-field {
  display: block;
  border: 1px solid transparent;
  box-sizing: border-box;
  padding: 14px 16px;
  width: 100%;
  height: 48px;
  border-radius: 40px;
  background-color: #f8f8fa;
  color: $mainColor;

  &::placeholder {
    font-size: 14px;
    line-height: 20px;
    color: #7e7e83;
  }

  &:hover {
    @include hover(border, 1px solid #d5d7de);
  }

  &:focus {
    @include hover(border-color, $mainColor);
  }

  &--textarea {
    font-family: inherit;
    border-radius: 16px;
    height: 148px;
    resize: none;
  }
}
